html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
}

/* .app {
  text-align: left;
} */

.header {
  background-color: white;
  height: 60px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  color: #222;
  border-bottom: 1px solid #4b4b4b;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.header-left{
  display: -ms-flexbox;
  display: flex;
}
.logo {
  height: 48px;
  color: #4b4b4b;
  margin: auto 10px auto 20px;
}

.title {
  line-height: 60px;
  color: #4b4b4b;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  margin: 0;
}

.weather {
  margin-right:10px;
}
.weather i{
  font-size: 30px;
}
.weather p{
  margin: 0;
  font-size: 12px;
  text-align: center;
}

.main {
  height: calc(100vh - 61px);
  display: -ms-flexbox;
  display: flex;
}

.place-list {
  height: 100%;
  border-right: 1px solid #4b4b4b;
  display: block;
  width: 400px;
}

.place-select-container {
  width: 100%;
  padding: 10px 10px 0 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #4b4b4b;
}

.place-list-num {
  font-size: 14px;
  padding: 5px;
  height: 19px;
  line-height: 19px;
}
.place-select {
  border: 1px solid #4b4b4b;
  width: 100%;
  border-radius: 5px;
  height: 30px;
  font-size: 14px;
}

.place-item-list {
  height: 100%;
  padding: 0 10px;
  overflow-y: auto;
  height: calc(100% - 70px);
}
.place-item {
  font-size: 12px;
  border: 1px solid #4b4b4b;
  margin-top: 10px;
  padding: 5px 10px;
  -webkit-box-shadow: 1px 2px 5px #888888;
          box-shadow: 1px 2px 5px #888888;
  border-radius: 3px;
}

.place-item p {
  margin: 10px 0;
}

.place-item:hover {
  cursor: pointer;
}

.pop-item {
  font-size: 12px;
}

.pop-item img {
  width: 100%;
  max-width: 300px;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
}

.map-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.btn-sidebar {
  z-index: 9999;
  position: absolute;
  color: #000;
  top: 15px;
  left: 15px;
  font-weight: bold;
  font-size: 20px;
  display: none;
}

@media screen and (max-width: 600px){
  .show {
    display: block;
  }

  .hide {
    display: none;
  }
  .header {
    height: 50px;
  }

  .logo {
    height: 38px;
    margin: auto 10px auto 15px;
  }

  .title {
    line-height: 50px;
  }

  .main {
    height: calc(100vh - 51px);
  }
  .btn-sidebar{
    display: block;
  }
  .pop-item img {
    max-width: 200px;
    height: 200px;
    -o-object-fit: cover;
       object-fit: cover;
  }
}




